import {
		mapState, 
    } from 'vuex';
import md5 from '../../kayida/common/md5.js'

export default {
	computed: {
	            ...mapState(['userInfo'])  
	        },
	data() {
		return {
			credit_current:0,
			credit_list:'',
			order_list:'',
			order_current:0,
			u_share:1,
			credit_type:0,
			order_q:0,
			noData:0,
			order_details:'',
			page:0,
			Height:0,
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
		}
	},
	methods: {
		credit(index) {
			if(this.credit_current !== index){
				this.credit_current = index
			}
			this.order_current = 0
		},
		order(index) {
			this.goTop()
			if(this.order_current !== index){
				this.order_current = index
			}
		},
		goTop() {
			this.scrollTop = this.old.scrollTop
			this.$nextTick(function() {
				this.scrollTop = 0
			});
		},
		lower(e) {
			this.page = this.page + 1
			let data = {
				user_id: this.userInfo.user_id,
				pageNo:this.page,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/apply/index',data,'GET').then(res => {
				if(!this.n) {
					uni.showLoading({
						title: '加载中...',
						mask: true
					})
						let list = res.data.retval
						if(res.data.retval.length < 1){
							this.n = 1
							uni.showToast({
								icon: 'none',
								title: '没有消息了'
							})
						}else{
							this.n = 0
							uni.showToast({
								icon: 'success',
								title: '加载成功'
							})
						}
						if(!this.n) {
							uni.showToast({
								icon: 'success',
								title: '加载成功'
							})
						}else {
							uni.showToast({
								icon: 'none',
								title: '没有消息了'
							})
						}
						this.order_details = this.order_details.concat(list)
						uni.hideLoading()
				}
			})
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success:(res) => {
				this.Height = res.screenHeight - 20 +'px'
			}
		})
		this.request.httpRequest('api/apply/nav','GET').then(res => {
			if(res.data.code === 1000){
				this.credit_list = res.data.retval
				this.order_list = res.data.retval[this.credit_current].child
			}else{
				uni.showToast({
					title:res.data.msg,
					icon:"none"
				})
			}
		})
		let data = {
			mode:1,
			type:this.credit_type,
			q:this.order_q,
			user_id: this.userInfo.user_id,
			token: this.userInfo.token,
			sign: md5(this.userInfo.user_id+this.userInfo.token)
		}
		this.request.httpRequest('api/apply/index',data,'GET').then(res => {
			this.order_details = res.data.retval
			if(this.order_details < 1) {
				this.noData = 1
			}else{
						this.noData = 0
					}
		})
	},
	watch:{
		credit_current(newval,oldval){
			this.goTop()
			this.credit_type = this.credit_list[newval].type
			this.order_current = 0
				if(newval == 4 || newval == 2 || newval == 3){
					this.u_share = 3
				}else{
					this.u_share = 1
				}
			let data = {
				mode:1,
				type:this.credit_type,
				q:this.order_q,
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/apply/index',data,'GET').then(res => {
				this.order_details = res.data.retval
				if(this.order_details < 1) {
					this.noData = 1
				}else{
						this.noData = 0
					}
			})
			this.request.httpRequest('api/apply/nav','GET').then(res => {
				if(res.data.code === 1000){
					this.order_list = res.data.retval[newval].child
				}else{
					uni.showToast({
						title:res.data.msg,
						icon:"none"
					})
				}
			})
		},
		order_current(newval,oldval){
			this.goTop()
			this.order_q = this.order_list[newval].q
			let data = {
				mode:1,
				type:this.credit_type,
				q:this.order_q,
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/apply/index',data,'GET').then(res => {
				this.order_details = res.data.retval
				if(this.order_details < 1) {
					this.noData = 1
				}else{
						this.noData = 0
					}
			})
		}
	}
}
